import "./App.css";
import NavigationBar from "./components/NavigationBar";
import ContentBody from "./components/ContentBody";
import React from "react";
import planetData from "./planetData";

function App(props) {
  const [currentPlanet, changeCurrentPlanet] = React.useState(planetData[0]);
  const [isFirstTime, setFirstTime] = React.useState(true);
  const [currentBacgroundColor, setBackgroundColor] = React.useState("#419EBB");

  const colors = [
    "#419EBB",
    "#EDA249",
    "#2D68F0",
    "#D14C32",
    "#D83A34",
    "#CD5120",
    "#1EC1A2",
    "#2D68F0",
  ];

  window.onresize = mobileScreenSize;
  window.onload = mobileScreenSize;
  let planetIsClicked = false;
  function showNewPlanetInfo(event) {
    let screenWidth = window.innerWidth;
    if (screenWidth < 540) {
      planetIsClicked = true;
      mobileScreenSize();
    }
    let newPlanet = event.target.getAttribute("id");
    changeCurrentPlanet(planetData[newPlanet]);
    setBackgroundColor(colors[newPlanet]);
    setFirstTime(true);
  }
  function notFirstTime() {
    setFirstTime(false);
  }

  function mobileScreenSize() {
    let screenWidth = window.innerWidth;
    if (screenWidth < 540 && planetIsClicked) {
      document.querySelector("#contentBody").style = "display:flex";
      document.querySelector("#stats").style = "display:flex";
    }
  }

  return (
    <>
      <NavigationBar changePlanet={showNewPlanetInfo} />
      <ContentBody
        currentClickedPlanet={currentPlanet}
        firstTime={isFirstTime}
        changeFirstTime={notFirstTime}
        background={currentBacgroundColor}
      />
    </>
  );
}

export default App;
