import React from "react";
import "./NavigationBar.css";

export default function NavigationBar(props) {
  function changeTextColor(event) {
    let mouseHoverElement = event.target.getAttribute("id");
    if (mouseHoverElement === "0") {
      event.target.style = "color : #419EBB";
    } else if (mouseHoverElement === "1") {
      event.target.style = "color: #EDA249";
    } else if (mouseHoverElement === "2") {
      event.target.style = "color: #2D68F0";
    } else if (mouseHoverElement === "3") {
      event.target.style = "color: #D14C32";
    } else if (mouseHoverElement === "4") {
      event.target.style = "color: #D83A34";
    } else if (mouseHoverElement === "5") {
      event.target.style = "color: #CD5120";
    } else if (mouseHoverElement === "6") {
      event.target.style = "color: #1EC1A2";
    } else {
      event.target.style = "color: #2D68F0";
    }
  }

  function resetColor(event) {
    event.target.style = "color: white";
  }

  return (
    <nav className="navbar navbar-expand-sm   container-fluid">
      <h2 className="navbar-brand">THE PLANETS</h2>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNavAltMarkup"
        aria-controls="navbarNavAltMarkup"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <i className="fas fa-bars navbar-toggler-icon"></i>
      </button>
      <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div className="navbar-nav">
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="0"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            MERCURY
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="1"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            VENUS
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="2"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            EARTH
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="3"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            MARS
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="4"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            JUPITAR
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="5"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            SATURN
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="6"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            URANUS
          </a>
          <a
            className="nav-item nav-link"
            onClick={props.changePlanet}
            id="7"
            onMouseOver={changeTextColor}
            onMouseLeave={resetColor}
            href="#image"
          >
            NEPTUNE
          </a>
        </div>
      </div>
    </nav>
  );
}
