import React from "react";
import PlanetImage from "./PlanetImage";
import PlanetDescription from "./PlanetDescription";
import MoreDescription from "./MoreDescription";
import Stats from "./Stats";
import "./ContentBody.css";
import "./Stats.css";

export default function ContentBody(props) {
  const [currentDescription, setNewDescription] = React.useState("overview");
  const [currentImageLink, setCurrentImageLink] = React.useState(
    props.currentClickedPlanet.images.planet
  );
  const [currentText, changeCurrentText] = React.useState(
    props.currentClickedPlanet.overview
  );
  const [currentPlanetName, changeCurrentPlanetName] = React.useState(
    props.currentClickedPlanet.name
  );

  React.useEffect(() => {
    if (props.firstTime) {
      setNewDescription("overview");
      setCurrentImageLink(props.currentClickedPlanet.images.planet);
      changeCurrentText(props.currentClickedPlanet.overview);
      changeCurrentPlanetName(props.currentClickedPlanet.name);
      document.querySelector("#internalStructure").style =
        "backgroundColor : none";
      document.querySelector("#surfaceGeology").style =
        "backgroundColor : none";
      props.changeFirstTime();
    }
  });

  function changeDescription(event) {
    let clickedElement = event.target;
    if (clickedElement.hasAttribute("id")) {
      changeScreenInfo(clickedElement);
    } else {
      let parent = clickedElement.parentNode;
      changeScreenInfo(parent);
    }
  }

  function changeScreenInfo(clickedElement) {
    let description = clickedElement.getAttribute("id");
    setNewDescription(description);
    if (description === "overview") {
      setCurrentImageLink(props.currentClickedPlanet.images.planet);
      changeCurrentText(props.currentClickedPlanet.overview);
    } else if (description === "internalStructure") {
      setCurrentImageLink(props.currentClickedPlanet.images.internal);
      changeCurrentText(props.currentClickedPlanet.structure);
    } else {
      setCurrentImageLink(props.currentClickedPlanet.images.geology);
      changeCurrentText(props.currentClickedPlanet.geology);
    }

    let first = document.querySelector("#overview");
    let second = document.querySelector("#internalStructure");
    let third = document.querySelector("#surfaceGeology");

    if (first.getAttribute("id") === description) {
      first.style = "backgroundColor : " + props.background;
    }
    if (second.getAttribute("id") === description) {
      second.style = "backgroundColor :" + props.background;
    }
    if (third.getAttribute("id") === description) {
      third.style = "backgroundColor : " + props.background;
    }
    if (first.getAttribute("id") !== description) {
      first.style = "backgroundColor : none";
    }
    if (second.getAttribute("id") !== description) {
      second.style = "backgroundColor : none";
    }
    if (third.getAttribute("id") !== description) {
      third.style = "backgroundColor : none";
    }
  }

  return (
    <>
      <div className="container" id="contentBody">
        <PlanetImage src={currentImageLink} />
        <div>
          <PlanetDescription heading={currentPlanetName} body={currentText} />
          <div id="moreDescription">
            <MoreDescription
              id="overview"
              name="OVERVIEW"
              number="01"
              clicked={changeDescription}
              currentClickedElement={currentDescription}
              color={props.background}
            />
            <MoreDescription
              id="internalStructure"
              name="INTERNAL STRUCTURE"
              number="02"
              clicked={changeDescription}
              currentClickedElement={currentDescription}
              color={props.background}
            />
            <MoreDescription
              id="surfaceGeology"
              name="SURFACE GEOLOGY"
              number="03"
              clicked={changeDescription}
              currentClickedElement={currentDescription}
              color={props.background}
            />
          </div>
        </div>

        <div id="stats" className="container">
          <Stats
            name="ROTATION TIME"
            stats={props.currentClickedPlanet.rotation}
          />
          <Stats
            name="REVOLUTION TIME"
            stats={props.currentClickedPlanet.revolution}
          />
          <Stats name="RADIUS" stats={props.currentClickedPlanet.radius} />
          <Stats
            name="AVERAGE TEMP."
            stats={props.currentClickedPlanet.temperature}
          />
        </div>
      </div>
    </>
  );
}
