import React from 'react';
import "./PlanetDescription.css";

export default function PlanetDescription(props){
    return(
        <div id = "descriptionContainer">
            <h1 id = "nameOfPlanet">{props.heading}</h1>
            <p id = "descriptionOfPlanet">{props.body.content}</p>
            <p>
                <span id = "source">Source: </span>
                <a href = {props.body.source} target = "_blank" id ="wikiPage"> Wikipedia
                <img src = "./assets/icon-source.svg" id = "wikiPageIcon"></img>
                </a>
            </p>
        </div>
    )
}